body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  background-color: #1db954;
  color: black;
}

button:hover {
  cursor: pointer;
  background-color: #1aa14a;
}

button:focus {
  outline: 0;
}
.button_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 50%;
  padding: 10px 25% 50px 25%;
}

.button_container button {
  border-radius: 20px;
  border: 1px black solid;
  background-color: white;
  width: 20%;
  height: 2em;
  font-weight: 500;
}

.button_container button:hover {
  background-color: #1db954;
  color: white;
  border-color: #1db954;
}

.button_container button:focus {
  background-color: #1db954;
  color: white;
  border-color: #1db954;
}

.expand_button {
  position: fixed;
  z-index: 2;
  bottom: 2vh;
  right: 2vw;
  border-radius: 50%;
  height: 3em;
  width: 3em;
  border: none;
}

p {
  margin: 0;
}

.App {
  text-align: center;
}

#main_title {
  font-size: calc(1.5em + 4vw);
  margin: 2.5vh;
  font-weight: bold;
  font-family: "Open Sans", sans-serif;
  color: #1db954;
}

#sub_title {
  font-size: calc(1em + 0.5vw);
  margin: 5px;
  font-weight: 200;
}

.Artist {
  color: rgb(0, 0, 0);
  width: 100%;
}

.Artist :hover {
  background: #dadada;
  cursor: pointer;
}

.unexpanded_artist_title {
  font-size: 30px;
  height: 1.7em;
  font-weight: 200;
}

.expanded_artist {
  display: -webkit-flex;
  display: flex;
}

.artist_details {
  width: calc(100% - 250px);
  text-align: left;
}

#expanded_title {
  margin-top: 5px;
  margin-left: 9%;
}

#expand_details {
  text-align: left;
  padding-left: 10%;
  font-size: 1.25rem;
  line-height: 200%;
}

a {
  text-decoration: none;
  color: inherit;
}

span {
  position: relative;
  z-index: 1;
}

span::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: -0.25em;
  right: -0.25em;
  background-color: #1db954;
  -webkit-transform-origin: bottom center;
          transform-origin: bottom center;
  -webkit-transform: scaleY(0.1);
          transform: scaleY(0.1);
  transition: all 0.1s ease-in-out;
}

span:hover::before {
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
  background-color: #1db954;
}

.img_box {
  /* Prevent vertical gaps */
  width: 250px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.img_box img {
  /* Just in case there are inline attributes */
  max-width: 250px !important;
  height: auto !important;
}

